// 头部组件
<template>
  <div class="header">
    <!-- 商家 -->
    <div class="content-wrapper">

      <div class="avatar">
        <img :src="seller.avatar" width="64" height="64" />
      </div>
      <div class="content">
        <div class="title">
          <div class="brand"></div>
          <span class="name">{{seller.name}}</span>
        </div>
        <div class="description">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
          <!-- 满减等优惠 -->
        <div v-if="seller.supports" class="support">
          <!-- 动态绑定classjson内的数据内的type对应的背景图 -->
          <span class="icon" :class="ClassMap[seller.supports[0].type]"></span>
          <span class="text">{{seller.supports[0].description}}</span>
        </div>
      </div>

      <div class="support-count" v-if="seller.supports" @click="showDetail">
          <span class="count">{{seller.supports.length+'个'}}</span>
          <i class="icon-keyboard_arrow_right"></i>
      </div>
    </div>
    <!-- 公告 -->
    <div class="bulletin-wrapper"  @click="showDetail">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
        <i class="icon-keyboard_arrow_right"> </i>
    </div>
    <!-- 背景图 -->
    <div class="background">
        <img :src="seller.avatar" width="100%" height="100%">
    </div>
    
    <!-- 悬浮层详情 -->
    <transition name="fade">
    <div class="detail" v-show="detailShow">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <!-- 星级组件 -->
          <div class="star-wrapper">
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <!-- 优惠类型信息 -->
          <ul v-if="seller.supports" class="supports">
            <li class="support-item" v-for="item in seller.supports">
              <span class="icon" :class="ClassMap[item.type]"></span>
              <span class="text">{{item.description}}</span>
            </li>
          </ul>
          <!-- 商家公告 -->
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
        </div>  
          <!-- 公告内容 -->
        <div class="bulletin">
          <p class="content">{{seller.bulletin}}</p>
        </div>
      </div>
    
      <!-- 关闭详情按钮 -->
      <div class="detail-close" @click="hideDetail">
        <i class="icon-close"></i>
      </div>
    </div>
    </transition>
  </div>
</template>

<script>
  // 引入星级评价组件
import star from 'components/star/star';

export default {
  // 父向子传参，页面传递变量在组件内使用
  props: {
    seller: {
      type: Object
    }
  },

  //data是一个function ,会return一个变量
  //在Vue实例化的时候会对data里面的变量进行遍历,给他添加get和set,修改dom
  data(){
    return{
      detailShow:false
    }
  },

  // 绑定事件
  methods:{
    showDetail(){
      this.detailShow = true;
    },
    hideDetail(){
      this.detailShow = false;
    }
  },
  
  // 生命周期
  created() {
    this.ClassMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
  },

  // 注册组件
  components:{
    star
  }
};
</script>


<style lang="stylus">
  // 引入样式
  @import "../../common/stylus/mixin"
  
.header
  position: relative
  background rgba(7,17,27,0.5)
  color #fff
  blur:10px
  overflow hidden
  .content-wrapper
    position relative
    display flex
    padding: 24px 12px 18px 24px
    font-size 12px
    .avatar
      img
        border-radius 3px
    .content
      margin-left 16px
      .title
        margin 2px 0 8px 0
        font-size 16px
        .brand
          display inline-block
          vertical-align top
          width 30px
          height 18px
          bg-image('brand')
          background-size 30px 18px
          background-repeat no-repeat
        .name
          margin-left 6px
          font-size 16px
          line-height 18px
          font-weight bold
      .description
        font-size 12px
        margin-bottom 10px
        line-height 12px
      .support
        .icon
          display: inline-block
          width: 12px
          height: 12px
          margin-right:4px
          background-size:12px 12px
          background-repeat:no-repeat
          &.decrease
            bg-image('decrease_1')
          &.discount
            bg-image('discount_1')
          &.guarantee
            bg-image('guarantee_1')
          &.invoice
            bg-image('invoice_1')
          &.special
            bg-image('special_1')
        .text
            // css影响宽高布局的样式都写在前面,样式写在后面
            line-height 12px
            font-size 10px
            vertical-align top
    .support-count
      position: absolute
      right: 12px
      bottom:14px
      padding:0 8px
      height:20px
      line-height:20px
      border-radius:14px
      background:rgba(0,0,0,0.2)
      text-align: center
      .count
        vertical-align top
        font-size 10px
      .icon-keyboard_arrow_right
        font-size 10px
        margin-left 2px
        line-height 18px

  .bulletin-wrapper
    position:relative
    height: 28px
    line-height: 28px
    padding: 0 22px 0 12px
    white-space: nowrap
    overflow: hidden
    text-overflow: ellipsis
    // font-size:0px
    background: rgba(7,17,27,0.2)
    .bulletin-title
      display inline-block
      vertical-align top
      margin-top 8px
      width 22px
      height 12px
      bg-image("bulletin")
      background-size 22px 12px
      background-repeat no-repeat
    .bulletin-text
      vertical-align top
      margin:0 4px
      font-size:10px
    .icon-keyboard_arrow_right
      position absolute
      font-size 10px
      right 12px
      top 8px
  .background
    position absolute
    top 0px
    left 0px
    width 100%
    height 100%
    z-index -1
    filter blur(10px)
  .detail
    position:fixed
    z-index:100
    top:0
    left:0
    width 100%
    height 100%
    overflow auto
    background rgba(7,17,27,0.8)
    // 悬浮层模糊
    -webkit-backdrop-filter: blur(5px);	
    backdrop-filter: blur(5px);	
    &.fade-enter-active, &.fade-leave-active {
        transition: opacity .5s
    }
    &.fade-enter, &.fade-leave-active {
        opacity: 0
    }

    .detail-wrapper
      width 100%
      min-height 100%
      padding-bottom 70px
      .detail-main 
        margin-top 64px
        .name
          line-height 16px
          text-align center
          font-size 20px
          font-weight 700
        .star-wrapper
          margin-top: 18px
          padding:2px 0
          text-align center
        .title
          display flex
          width 80%
          margin:30px auto 24px auto
          .line
            flex:1
            position relative
            top: -6px
            border-bottom 1px solid rgba(255,255,255,0.2)
          .text
            padding:0 12px
            font-size 14px
        .supports
          padding 0 0 0 36px
          .support-item
            color white
            padding 0 6px 12px 16px
            .text
              vertical-align middle
              font-size 12px
              font-weight 200
              color rgb(255,255,255)
              line-height 12px
            .icon
              display inline-block
              vertical-align top
              width 16px
              height 16px
              margin-right 6px
              background-size 100% 100%
              background-repeat no-repeat
              &.decrease
                bg-image('decrease_2')
              &.discount
                bg-image('discount_2')
              &.guarantee
                bg-image('guarantee_2')
              &.invoice
                bg-image('invoice_2')
              &.special
                bg-image('special_2')
      .bulletin
        width 80%
        margin 0 auto
        .content
          line-height 24px
          font-size 12px
          text-indent 2em
    .detail-close
      position relative
      width 32px
      height 32px
      margin:-64px auto 0px auto 
      clear:both
      font-size 32px
      color rgba(255,255,255,0.5)
    
</style>


